import {UserModel} from "../../model/user";
const  userModel = new UserModel()
export default {
  data() {
    return {
        user:{}
    };
  },
  methods: {
      // 个人中心
      _getUserInfo(){
          userModel.getUerInfo().then((res) => {
              if(res.code < 400){
                  this.user = res.data
              }
          })
      },
      toRecharge(){
          uni.navigateTo({
        url: "/pagesA/recharge/recharge"
      });
      },
      toBalanceDetails(){
          uni.navigateTo({
             url: "/pagesA/balance_details/balance_details"
           });
      }
  },
  components: {},
  onLoad: function() {

  },
  onShow: function() {
      this._getUserInfo()
  },
  onHide: function() {}
};
